.product-list {
  display: flex;
  display: grid;
  grid-template-columns: auto auto auto auto;
  gap: 6px;
}

.card {
  width: 280px;
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  overflow: hidden;
  text-align: center;
}

.card-img-top {
  height: 200px;
  object-fit: cover;
}

.card-body {
  padding: 10px;
}

.card-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
}

.card-text {
  font-size: 13px;
  margin-bottom: 20px;
}

.btn-primary {
  display: block;
  align-content:stretch;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;

  padding: 10px 27px;
  font-size: 16px;
  cursor: pointer;
  text-decoration: none;
}

.btn-primary:hover {
  background-color: #0069d9;
}
