.container{
    max-width: 1140px;
    margin: 0  auto;
    padding: 0;
  }
  
  .single-container {
      margin-top: 50px;
    }
  
  
    .img-fluid {
      height: 400px;
      object-fit: cover;
    }
    
    h2 {
      font-size: 36px;
      font-weight: bold;
      margin-bottom: 20px;
    }
    
    .lead {
      font-size: 24px;
      font-weight: bold;
      margin-bottom: 20px;
    }
    
    p {
      font-size: 18px;
      margin-bottom: 20px;
    }
    
    .spinner-container {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100vh;
    }
    
    .spinner {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      border: 5px solid #ccc;
      border-top-color: #007bff;
      animation: spin 1s ease-in-out infinite;
    }
    
    @keyframes spin {
      to {
        transform: rotate(360deg);
      }
    }