* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Gilroy";
}
.container {
  max-width: 1240px;
  margin: 0 auto;
  padding: 0;
}
.slide {
  text-align: center;
  width: 300px;
  height: 200px;
}

.App {
  text-align: center;
}

.head {
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 20px;
  padding: 0 30px;
  padding-bottom: 5px;
  box-shadow: 12px 4px 24px 0px rgba(0, 0, 0, 0.06);
}

.head .logo {
  display: flex;
  gap: 20px;
  margin-right: 60px;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 15px;
}

.head .links {
  display: flex;
  gap: 25px;
  justify-content: center;
  align-items: center;
}

.head .links a {
  text-decoration: none;
  color: #000;
  font-size: 15px;
  font-weight: 600;
}

.head .lan-sel {
  display: flex;
  gap: 10px;
  padding: 0px 10px 0px 20px;
  font-weight: lighter;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.head .lan-sel h3 {
  font-weight: lighter;
}

.head .login {
  display: flex;
  gap: 10px;
  color: #4e72ca;
align-items: center;

}

.head .login a {
  text-decoration: none;
  font-weight: bold;
}

.sl {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
  position: relative;
}

.sl {
  margin-top: 50px;
}

.leftArrow {
  position: absolute;
  cursor: pointer;
  padding: 0px 1200px 110px 0px;
}

.leftArrow img {
  transform: scale(1.2);
}

.rightArrow {
  position: absolute;
  cursor: pointer;
  padding: 0px 0px 110px 1200px;
}

.rightArrow img {
  transform: scale(1.2);
}

.loginPage {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 150px;
}

.right {
  display: flex;
  flex-direction: column;
  /* margin-bottom: 150px; */
}

.right .text {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.right .text h3 {
  color: #304fff;
  text-align: center;
  font-family: Gilroy;
  font-size: 17px;
  font-style: normal;
  font-weight: bold;
  line-height: 23px;
}

.right .inputs {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 50px;
  text-align: left;
}

.inputs input {
  padding: 20px;
  border-radius: 5px;
  border: 1px solid rgba(151, 151, 151, 0.48);
  background: #fff;
}

.inputs label {
  color: #787993;
  text-align: left;
  font-size: 17px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.inputs button {
  padding: 20px;
  border-radius: 5px;
  background: #41d481;
  border: none;
  margin-top: 20px;
  cursor: pointer;
}
.inputs button:hover {
  padding: 20px;
  transition: 1s ease-in-out all;
  border-radius: 5px;
  color: #fff;
  background: #42ac70;
  border: none;
  margin-top: 20px;
  cursor: pointer;
}

.reset {
  display: flex;
  flex-direction: column;
  margin-top: 100px;
  gap: 20px;
}

.reset h2 {
  width: 100%;
  text-align: center;
  border-bottom: 1px solid #000;
  line-height: 0.1em;
  margin: 10px 0 20px;
  font-size: 15px;
}

.reset h2 span {
  background: #fff;
  padding: 0 10px;
}

.reset a {
  text-decoration: none;
  font-weight: bold;
}
